body{
    background-color: #dbe7eb;
}

.titulo{
    font-size: 50px;
    font-weight: 700;
    color: #61dbfb;
}

button{
    width: 3em;
    height: 3em;
    font-size: 1.5em;
    border: none;
    border-radius: 50%;
    margin: 0.3em;
}

button:hover{
    cursor: pointer;
}

.wrapper{
    background-color: #282a36;
    border-radius: 1em;
    padding: 1em;
    box-shadow: 0px 4px 20px #282a36;
}

.orange{
    background-color: #FF9500;
    color: white;
    font-weight: 600;
}

.lightgrey{
    background-color: lightgrey;
    color: black;
    font-weight: 600;
}

.darkgrey{
    background-color: #505050;
    color: white;
    font-weight: 500;
    font-weight: 600;
}

.resultado{
    color: white;
    text-align: right;
    padding-right: 0.5em;
    font-size: 3.5em;
    font-weight: 400;
}

.preVisualizacao{
    color: rgb(190, 190, 190);
    text-align: right;
    padding-right: 1.2em;
    margin-bottom: -40px;
    margin-top: -40px;
    font-size: 1.5em;
    font-weight: 400;
}

#zero{
    width: 6.5em;
    border-radius: 1.4em;
}

@media(max-width: 409px) {
    button{
        width: 2.3em;
        height: 2.3em;
        font-size: 1.5em;
        border: none;
        border-radius: 50%;
        margin: 0.3em;
    }
    #zero{
        width: 5.3em;
        border-radius: 1.4em;
    }
    .titulo{
        font-size: 33px;
        font-weight: 700;
        margin-top: -33px;
        margin-bottom: 5px;
    }
}

